export default {
  // namespaced: true,
  state: {
    //
    currentSong: {},
    //
    playstate: false
  },
  getters: {

  },
  mutations: {
    setcurrentSongM(state, song) {
      state.currentSong = song
      console.log('----------' + JSON.stringify(state.currentSong))
    },
    setplaystateM(state, playstate) {
      state.playstate = playstate
      console.log('----------' + JSON.stringify(state.playstate))
    }
  },
  actions: {
    //
    setcurrentSongA({
      state,
      commit
    }, song) {
      commit('setcurrentSongM', song)
      commit('setplaystateM', true)
    }
  }
}
